html {
  overflow: hidden;
}

body {
  margin: 0;
  scroll-behavior: smooth;
  background-repeat: no-repeat;
  background-color: $primary-color;
  height: 100vh; 
  overflow-x: hidden;
	overflow-y: auto;
	perspective: 1px;
  transform-style: preserve-3d;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  @media (min-width: 768px) {
    background-attachment: fixed;
    background-image: url('/img/bckgr.jpg');
  }
}

header {
  position: absolute;
  top: 0;

  @media(min-width: 768px) {
    position: relative;
  }
}

.hero {

  min-height: 600px;
  max-width: 750px;

  @media (min-width: 1680px) {
    max-width: 850px;
  }

  p {
    color: #fff;
    font-weight: 400;
  }

  .btn {
    min-width: 230px;
    box-shadow: rgba(0, 0, 0, 0.6) 0px 1px 4px 0px;
  }

  .btn-lefty {
    position: relative;
    &:before {
      content: "";
      background: no-repeat 0 0;
      background-image: url(data:image/png;base64,);
      bottom: -26px;
      height: 26px;
      left: -20px;
      position: absolute;
      width: 165px;
    }
  }
  .btn-righty {
    position: relative;
    &:after {
      content: "";
      background: no-repeat 100% 0;
      background-image: url(data:image/png;base64,);
      bottom: -26px;
      height: 26px;
      position: absolute;
      right: -20px;
      width: 165px;
    }
  }

  &--illustration {
    order: 2;

    @media (min-width: 768px) {
      opacity: 40%;
      order: 0;
      margin-top: calc(-100px + 5vw);
    }
  }
}

main {
  perspective: 2px;
}

.how {
  min-height: 500px;
  background-color:#eff2fb;

  li:before {
    color: $primary-color;
  }
}

.need-help {
  background-color:#fff;
  h3 {
    color: $accent-color;
  }
  p {
    color: #2f2e2e;
  }
  li:before {
    color: $accent-color;
  }
}

.take-action {
  background-color:#eff2fb;
  min-height: 300px;

  .illustration {
    display: block;
    margin: auto;
    width: 50%;
    max-width: 400px;

    @media (min-width: 576px) {
      position: absolute;
      right: 5%;
      bottom: 0;
      width: 25%;
    }
    
  }

  .btn {
    &:before {
    content: "";
    background: no-repeat 0 0;
    background-image: url(data:image/png;base64,);
    bottom: -26px;
    height: 26px;
    left: -20px;
    position: absolute;
    width: 165px;
    }
    &:after {
    content: "";
    background: no-repeat 100% 0;
    background-image: url(data:image/png;base64,);
    bottom: -26px;
    height: 26px;
    position: absolute;
    right: -20px;
    width: 165px;
    }
  }

}
